
.clear {
  clear: both;
}
/*transition */
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.shopCart {
  width: 100%;
  height: 1.0rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #141d27;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 10;
}
.cart-lt {
  padding: 0 0.24rem;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.circle {
  display: inline-block;
  background: #2B333B;
  color: rgba(225, 225, 225, 0.4);
  width: 44px;
  height: 44px;
  font-size: 24px;
  border: 6px solid #141d27;
  border-radius: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: absolute;
  left: 12px;
  top: -10px;
}
.cart-lt div.active {
  background: #3C96E9;
  color: #fff;
}
.totalCount {
  position: absolute;
  left: 0.55rem;
  top: -0.1rem;
  font-size: 0.18rem;
  color: #fff;
  background: red;
  padding: 3px 5px;
  border-radius: 50%;
}
.cart-lt span.active {
  color: #fff;
}
.cart-rt {
  background: #2B333B;
  font-weight: 700;
  color: rgba(225, 225, 225, 0.4);
  font-size: 0.28rem;
  width: 2.1rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100%;
}
.cart-rt.active {
  background: #55DB6C;
  color: #fff;
}
.cart-lt-main {
  font-size: 0.32rem;
  color: rgba(225, 225, 225, 0.4);
}
.cart-lt-main span:nth-of-type(1) {
  margin-left: 1.1rem;
}
.cart-lt-main span:nth-of-type(2) {
  margin: 0 0.1rem;
  font-weight: 200;
}
/* transition */
.slide-enter-active,
.slide-leave-active {
  transition: all 0.5s;
}
.slide-enter,
.slide-leave-active {
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}
/* cart list */
.cart-list {
  width: 100%;
  position: absolute;
  bottom: 1.0rem;
  left: 0;
  z-index: 1;
  background: #fff;
}
.list-head {
  background: #ECEFF1;
  color: #666666;
  font-size: 0.28rem;
  line-height: 0.8rem;
  padding: 0 0.36rem;
}
.list-clear {
  float: right;
  color: #24A6DE;
}
.list-content {
  max-height: 4.7rem;
  overflow: auto;
}
.list-content li {
  padding: 0.24rem 0.36rem;
  height: 0.96rem;
  box-sizing: border-box;
  line-height: 0.48rem;
}
.l-f-name {
  float: left;
  font-size: 0.28rem;
}
.list-rt {
  float: right;
}
.l-f-price {
  float: left;
  font-size: 0.2rem;
  color: red;
  margin-right: 0.24rem;
}
.l-f-price span:nth-of-type(2) {
  font-weight: bold;
  font-size: 0.28rem;
}
.list-content .addgoods {
  position: relative;
  float: left;
  top: 0.02rem;
}
.list-mask {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3);
}
/* 小球动画*/
.ball {
  position: fixed;
  left: 32px;
  bottom: 22px;
  z-index: 200;
  transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
}
.ball .inner {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #00a0dc;
  transition: all 0.4s linear;
}



